const button = document.querySelector("button");
const inners = document.querySelectorAll(".inner");
const backs = document.querySelectorAll(".back");
const balance = document.querySelector(".back2");
const bonus = document.querySelector(".back3");

let delay = 90;
let balancePoint = 0;
let totalBonus = 0;
let myInterval;
let extra1 = 0;
let extra2 = 0;

button.addEventListener("click", () => {
  balancePoint = 20;
  button.disabled = true;
  myInterval = setInterval(flipCard, 2500);
});

async function flipCard() {
  await randomNumber();
  await flipAll();
  await returnFlip();
}

function checkMatch3(a, b, c) {
  return new Promise((res) => {
    if (a.textContent === b.textContent && b.textContent === c.textContent) {
      a.style.backgroundColor = "red";
      b.style.backgroundColor = "red";
      c.style.backgroundColor = "red";
      extra1 += 1;
      balancePoint += 3 * parseInt(extra1);
      totalBonus += 3 * parseInt(extra1);
    }
    res();
  });
}

function checkMatch4(a, b, c, d) {
  return new Promise((res) => {
    if (
      a.textContent === b.textContent &&
      b.textContent === c.textContent &&
      c.textContent === d.textContent
    ) {
      a.style.backgroundColor = "red";
      b.style.backgroundColor = "red";
      c.style.backgroundColor = "red";
      d.style.backgroundColor = "red";
      extra2 += 1;
      balancePoint += 6 * parseInt(extra2);
      totalBonus += 6 * parseInt(extra2);
    }
    res();
  });
}

async function randomNumber() {
  balancePoint--;
  if (balancePoint < 0) {
    button.disabled = false;
    clearInterval(myInterval);
  }
  balance.innerHTML = balancePoint;
  let numbers = [];
  return new Promise((res) => {
    for (let i = 0; i < 9; i++) {
      let random = Math.floor(Math.random() * 9);
      numbers.push(random);
    }

    backs.forEach((back, index) => {
      back.innerHTML = numbers[index];
    });

    checkMatch3(backs[0], backs[1], backs[2]);
    checkMatch3(backs[3], backs[4], backs[5]);
    checkMatch3(backs[6], backs[7], backs[8]);
    checkMatch3(backs[0], backs[3], backs[6]);
    checkMatch3(backs[1], backs[4], backs[7]);
    checkMatch3(backs[2], backs[5], backs[8]);
    checkMatch3(backs[0], backs[4], backs[8]);
    checkMatch3(backs[2], backs[4], backs[6]);
    checkMatch3(backs[0], backs[1], backs[3]);
    checkMatch3(backs[1], backs[2], backs[5]);
    checkMatch3(backs[3], backs[6], backs[7]);
    checkMatch3(backs[5], backs[7], backs[8]);
    checkMatch3(backs[1], backs[3], backs[4]);
    checkMatch3(backs[1], backs[4], backs[5]);
    checkMatch3(backs[3], backs[4], backs[7]);
    checkMatch3(backs[4], backs[5], backs[7]);
    checkMatch3(backs[0], backs[2], backs[4]);
    checkMatch3(backs[6], backs[4], backs[5]);
    checkMatch3(backs[0], backs[4], backs[6]);
    checkMatch3(backs[2], backs[4], backs[8]);
    checkMatch3(backs[3], backs[1], backs[5]);
    checkMatch3(backs[1], backs[3], backs[7]);
    checkMatch3(backs[3], backs[5], backs[7]);
    checkMatch3(backs[1], backs[5], backs[7]);
    checkMatch4(backs[0], backs[2], backs[6], backs[8]);
    checkMatch4(backs[1], backs[3], backs[5], backs[7]);

    res();
  });
}

async function flipAll() {
  if (totalBonus !== 0) {
    bonus.innerHTML = `+${totalBonus - 1}`;
  } else {
    bonus.innerHTML = "";
  }

  await flipEach(inners[0]);
  await flipEach(inners[1]);
  await flipEach(inners[2]);
  await flipEach(inners[3]);
  await flipEach(inners[4]);
  await flipEach(inners[5]);
  await flipEach(inners[6]);
  await flipEach(inners[7]);
  await flipEach(inners[8]);
}

function flipEach(position) {
  return new Promise((res) => {
    setTimeout(() => {
      position.classList.add("flip");
      res();
    }, delay);
  });
}

function returnFlip() {
  return new Promise((res) => {
    setTimeout(() => {
      inners.forEach((inner) => inner.classList.remove("flip"));
      res();
      clearBackground();
      totalBonus = 0;
      extra1 = 0;
      extra2 = 0;
    }, delay * 10);
  });
}

function clearBackground() {
  backs.forEach((back) => {
    back.style.backgroundColor = "white";
  });
}
